import React from 'react';
import {
    Input,
    InputLabel,
    Select,
    MenuItem
} from '@material-ui/core'

const SearchBox = () => {

    return(
        <div className="search-box">
            <div>
                <InputLabel>Search</InputLabel>
                <Input type="text" placeholder="Ej. Hitting"/>
            </div>
            
            <div>
                <InputLabel>Sport</InputLabel>
                <Select value={0}>
                    <MenuItem value={0}>Baseball</MenuItem>
                    <MenuItem value={1}>Softball</MenuItem>
                </Select>
            </div>
            
            <div>
                <InputLabel>Skill</InputLabel>
                <Select value={0}>
                    <MenuItem value={0}>Hitting</MenuItem>
                    <MenuItem>Pitching</MenuItem>
                </Select>
            </div>
        </div>
    );
}

export default SearchBox;