import React from 'react';
import { 
    Card,
    CardContent,
    Typography,
    Link,
} from '@material-ui/core';
import image from '../../../content/images/thumbnail.png';

const DrillCard = ({title, content}) => (
    <Card className="card" style={{margin: 20}}>
        <CardContent className="card-content">
            <img className="card-thumbnail" src={image} alt="thumbnail"/>
            <div className="card-preview">
                <Typography variant="h5"><Link href="drill">{title}</Link></Typography>
                <Typography variant="body1">{content}</Typography>
            </div>
        </CardContent>
    </Card>    
)

export default DrillCard;