import React from 'react';
import {
    AppBar, 
    Typography,
    Toolbar,
    IconButton,
    Container
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import DrillCard from '../components/DrillCard/DrillCard';
import SearchBox from '../components/SearchBox';

const textPlaceHolder = `
Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus efficitur dapibus libero, et pulvinar sapien maximus nec. Vivamus nisi nisl, consectetur id est id, ornare molestie arcu. Mauris et est imperdiet, placerat purus efficitur, dapibus libero. Pellentesque imperdiet euismod turpis...
`

const Drills = () => {
    return (<>
        <AppBar className="nav-bar" position="sticky">
            <Toolbar>
                <IconButton color="inherit">
                    <MenuIcon  />
                </IconButton>
                <Typography>LOGO</Typography>
            </Toolbar>
        </AppBar>
        <Container className="drills-container">
            <SearchBox />
            <div className="drills-list">
                <DrillCard style={{width: "1000px"}} title="Drill 1" content={textPlaceHolder}/>
                <DrillCard style={{width: "1000px"}} title="Drill 2" content={textPlaceHolder}/>
            </div>
        </Container>
        
        </>
    )
}

export default Drills;